import React,{Component} from 'react';

import {Image, Jumbotron, Fade} from 'react-bootstrap';
import {
    Container,
    Row,
    Col,
    Media
} from 'reactstrap';
import myPic from './../data/myPic.jpg';

class Home extends Component{


    state = {
        onScreen: false
    }

    componentDidMount = () => {

        window.setTimeout(()=>{
            this.setState({onScreen: true}); 
          },500);
        
          console.log(this.state.onScreen);
        
    }

    render(){
        
        const imgStyle = {
            maxHeight: 512,
            maxWidth: 512
          }
        return(
            <Container>
                 <Row>
                     <Media>
                         <Col md={{size:3, offset:0.5}} xs={{size:3, offset:0.5}}>
                             <Media href="/">
                                
                                 <Image src={myPic} style={imgStyle} thumbnail/>
                             </Media>
                         </Col>
                         <Col md={{size:7, offset:1}} xs={{size:7, offset:1}}>

 {/* <Button onClick={this.toggle}>ClickMe</Button> */}
                         <Fade in={this.state.onScreen}>
                         <Jumbotron>
                             <Container>
                                 <Media body left>
                                 <Media heading>
                                     Hello there :) ..!!!
                                 </Media >
                               
                                     I'm Anmol Mishra, graduate Indian Institute of Technology, Guwahati - Batch of 2019. Majored 
                                     in Engineering Physics with a passion for technology.<br/>I'm looking for opportunities in 
                                     consultancy, product management and development role.
                               
                                 </Media>
                             </Container>
                         </Jumbotron>
                         </Fade>
                         </Col>
                     </Media>
                 </Row>
             </Container>
        )
    }
}



export default Home;