class Resume extends Component{
    render(){
        return(
            <Container style={{justifyContent: "center", alignItems: 'center', display:'flex'}}>
                <object width="65%" height="500" data={pdfFile} type="application/pdf" name="Anmol">This is resume file of Anmol. If you can't see this, please note we are trying to fix the issue. <br/> In the meanwhile please click <a href="https://drive.google.com/file/d/1JbT1i3RVAEQpkqzGUzmkSWXiqL8dwYl5/view?usp=sharing">here</a> to see resume.</object>
            </Container>    
        )
    }
}

export default Resume;