import React, { Component } from 'react';
import {
    Navbar, 
    NavItem,
    Container,
    NavbarBrand,
    NavbarToggler,
    Collapse,
    Nav
} from 'reactstrap';
import {NavLink} from 'react-router-dom';

class AppNavbar extends Component{


    state = {
        isOpen: false
    }
    

    toggle = () => {
        this.setState({
            isOpen: !this.state.isOpen
        });
    }

    // componentDidMount= () =>{
    //     if(this.state.isOpen===true)
    //         this.isOpen = false;
    //     this.toggle();
    //     console.log("BC")
    // }



    render(){

       
        return(
            <div>
            <Navbar color="dark" dark expand="md" className="mb-5">
                <Container>
                    <NavbarBrand href="/"></NavbarBrand>
                    <NavbarToggler onClick={this.toggle}></NavbarToggler>
                    <Collapse isOpen={this.state.isOpen} navbar>
                        <Nav className="mx-auto" tabs>
                            
                            <NavItem className="navbar-text mr-3">
                            <div>
                                <NavLink to="/">About</NavLink>
                            </div>
                            </NavItem>
                            <NavItem className="navbar-text mr-3"> 
                            <div>
                                <NavLink to="/resume">Resume</NavLink>
                            </div>
                            </NavItem>
                            <NavItem className="navbar-text mr-3">
                            <div>
                                <NavLink to="/contact">Contact</NavLink>
                            </div>
                            </NavItem>
                        </Nav>
                    </Collapse>
                </Container>
            </Navbar>




                 {/* <Container>
                 <Row>
                     <Media>
                         <Col md={{size:3, offset:0.5}} xs={{size:3, offset:0.5}}>
                             <Media href="/">
                                
                                 <Image src={a} style={imgStyle} thumbnail/>
                             </Media>
                         </Col>
                         <Col md={{size:7, offset:1}} xs={{size:7, offset:1}}>


                         <Fade in={this.state.isOpen}>
                         <Jumbotron>
                             <Container>
                                 <Media body left>
                                 <Media heading>
                                     Hello there :) ..!!!
                                 </Media >
                               
                                     I'm Anmol Mishra, graduate Indian Institute of Technology, Guwahati - Batch of 2019. Majored 
                                     in Engineering Physics with a passion for technology. I'm looking for opportunities in 
                                     consultancy, product management and development role.
                               
                                 </Media>
                             </Container>
                         </Jumbotron>
                         </Fade>
                         </Col>
                     </Media>
                 </Row>
             </Container> */}
            
</div>
        )
    }
    
}

export default AppNavbar;
