import React, {Component} from 'react';
import { 
    Navbar,
    Nav,
    NavItem,
    NavLink
 } from 'reactstrap';

class Footer extends Component{

    render(){
        return(
            <div>
                <Navbar color="light" light sticky="bottom">
                <Nav className="ml-auto">
                            
                            <NavItem className="navbar-text mr-3">
                            <NavLink href="https://www.linkedin.com/in/anmol9197/" target="_blank">LinkedIn</NavLink>
                            </NavItem>
                            <NavItem className="navbar-text mr-3"> 
                            <NavLink href="https://github.com/Anmol-Mishra" target="_blank">Github</NavLink>
                            </NavItem>
                            <NavItem className="navbar-text mr-3">
                                <NavLink href="/" disabled>Twitter</NavLink>
                            </NavItem>
                        </Nav>
                </Navbar>
            </div>
        )
    }

}

export default Footer;